<template>
  <div class="about">
    <h1>Ruta protegida</h1>
    {{ token }}
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["token"]),
  },
  methods: {
    async protectedData() {
      try {
        const res = await fetch(
          "https://backendmodelo.herokuapp.com/api/dashboard",
          {
            headers: {
              "Content-Type": "application/json",
              "auth-token": this.token,
            },
          }
        );
        const resDB = await res.json();
        console.log(resDB);
      } catch (error) {
        console.log(error);
      }
    },
  },
  created(){
    this.protectedData();
  }
};
</script>